import {
    getUserGroups,
    getUserFriends,
    getUserFriendsGroups,
    getGroupsById
} from '../vk-api';

import {
    map,
    filter,
    has,
    chunk,
    sortBy
} from 'lodash';

const ACTIONS = {
    CHANGE_USER_GROUPS: "USER_CHANGE_USER_GROUPS",
    CHANGE_USER_GROUPS_LOADING: "USER_CHANGE_USER_GROUPS_LOADING",
    CHANGE_USER_FRIENDS: "USER_CHANGE_USER_FRIENDS",
    CHANGE_USER_FRIENDS_LOADING: "CHANGE_USER_FRIENDS_LOADING",
    CHANGE_USER_FRIENDS_GROUPS: "USER_CHANGE_USER_FRIENDS_GROUPS",
    CHANGE_USER_FRIENDS_GROUPS_LOADING: "USER_CHANGE_USER_FRIENDS_GROUPS_LOADING",
    CHANGE_USER_FRIENDS_GROUPS_ANALYZE: "USER_CHANGE_USER_FRIENDS_GROUPS_ANALYZE"
};

const initialState = {
    userGroups: null,
    userGroupsIsLoading: false,
    userFriends: null,
    userFriendsIsLoading: false,
    userFriendsGroups: null,
    userFriendsGroupsIsLoading: false,
    userFriendsGroupsAnalyzeProgress: 0,

};

const UserReducer = (state = initialState, action) => {
    const payload = action.payload;
    console.log(action.type, payload);
    switch (action.type) {
        case ACTIONS.CHANGE_USER_GROUPS:
            return {
                ...state,
                userGroups: payload.groups,
                userGroupsIsLoading: payload.isLoading
            };
        case ACTIONS.CHANGE_USER_GROUPS_LOADING:
            return {
                ...state,
                userGroupsIsLoading: payload.isLoading
            };
        case ACTIONS.CHANGE_USER_FRIENDS:
            return {
                ...state,
                userFriends: payload.friends,
                userFriendIsLoading: payload.isLoading
            };
        case ACTIONS.CHANGE_USER_FRIENDS_LOADING:
            return {
                ...state,
                userFriendsIsLoading: payload.isLoading
            };
        case ACTIONS.CHANGE_USER_FRIENDS_GROUPS_LOADING:
            return {
                ...state,
                userFriendsGroupsIsLoading: payload.isLoading
            };
        case ACTIONS.CHANGE_USER_FRIENDS_GROUPS:
            return {
                ...state,
                userFriendsGroups: payload.groups,
                userFriendsGroupsIsLoading: payload.isLoading
            };
        case ACTIONS.CHANGE_USER_FRIENDS_GROUPS_ANALYZE:
            return {
                ...state,
                userFriendsGroupsAnalyzeProgress: payload.value
            };
        default:
            return state;
    }
};

export const fetchUserGroups = (authToken, userId) => async (dispatch) => {
    dispatch({
        type: ACTIONS.CHANGE_USER_GROUPS_LOADING,
        payload: {
            isLoading: true
        }
    });
    const groups = (await getUserGroups(authToken, userId)).data.response.items;
    dispatch({
        type: ACTIONS.CHANGE_USER_GROUPS,
        payload: {
            groups,
            isLoading: false
        }
    });
};

export const fetchUserFriends = (authToken, userId) => async (dispatch) => {
    dispatch({
        type: ACTIONS.CHANGE_USER_FRIENDS_LOADING,
        payload: {
            isLoading: true
        }
    });
    const friends = filter((await getUserFriends(authToken, userId)).data.response.items, friend => {
        return has(friend, 'deactivated') === false;
    });
    dispatch({
        type: ACTIONS.CHANGE_USER_FRIENDS,
        payload: {
            friends,
            isLoading: false
        }
    });
};

const compressArray = original => {
    return original.reduce((b, c) => ((b[b.findIndex(d => d.el === c)] || b[b.push({
        el: c,
        count: 0
    }) - 1]).count++, b), []);
};

export const fetchUserFriendsGroups = (authToken, userGroups, friends) => async (dispatch) => {
    dispatch({
        type: ACTIONS.CHANGE_USER_FRIENDS_GROUPS_LOADING,
        payload: {
            isLoading: true
        }
    });
    const chunks = chunk(friends, 25);
    let groups = [];
    for (let i = 0; i < chunks.length; ++i) {
        const friendGroups = (await getUserFriendsGroups(authToken, map(chunks[i], chunk => {
            return chunk.id;
        }))).data.response;
        await new Promise(resolve => setTimeout(resolve, 3000));
        groups = groups.concat(friendGroups);
        dispatch({
            type: ACTIONS.CHANGE_USER_FRIENDS_GROUPS_ANALYZE,
            payload: {
                value: Math.floor((i + 1) / chunks.length * 100)
            }
        })
    }
    const rankedGroups = filter(map(sortBy(compressArray(groups), 'count').reverse(), group => {
        return group.el;
    }), group => {
        return map(userGroups, el => {
            return el.id
        }).includes(group) === false;
    }).slice(0, 50);
    dispatch({
        type: ACTIONS.CHANGE_USER_FRIENDS_GROUPS,
        payload: {
            groups: (await getGroupsById(authToken, rankedGroups)).data.response,
            isLoading: false
        }
    })
};

export default UserReducer;