import React from 'react';
import connect from '@vkontakte/vkui-connect';
import {connect as ReactConnect} from 'react-redux';
import {Root, View, Panel} from '@vkontakte/vkui';
import {changeView, changePanel, changeFetchedUser, changeAuthToken} from './Redux/mainReducer';
import MainPanel from './Panels/Main/MainPanel';
import FriendGroupsPanel from './Panels/FriendGroups/FriendGroupsPanel';
import '@vkontakte/vkui/dist/vkui.css';

class App extends React.Component {
    
    componentDidMount() {

        const { changeFetchedUser, changeAuthToken } = this.props;

        connect.subscribe((e) => {
            switch (e.detail.type) {
                case
                    'VKWebAppGetUserInfoResult':
                    changeFetchedUser(e.detail.data);
                    break;
                case
                    'VKWebAppAccessTokenReceived':
                    changeAuthToken(e.detail.data.access_token);
                    break;
                case 
                    'VKWebAppCallAPIMethodFailed':
                    console.log(e.detail.type, e.detail.data);
                    break;
                default:
                    console.log(e.detail.type);
            }
        });

        connect.send('VKWebAppGetUserInfo', {});
        connect.send('VKWebAppGetAuthToken', {
            'app_id': 6921887,
            'scope': 'groups,friends'
        });
    }

    go = (e) => {
        // this.setState({activePanel: e.currentTarget.dataset.to})
        this.props.changePanel(e.currentTarget.dataset.to, null)
    };

    render() {

        const {activeView, activePanel} = this.props;

        return (
            <Root activeView={activeView}>
                <View id="main" activePanel={activePanel}>
                    <Panel id="main">
                        <MainPanel/>
                    </Panel>
                    <Panel id="friendGroups">
                        <FriendGroupsPanel/>
                    </Panel>
                </View>
            </Root>

        );
    }
}

const mapStateToProps = state => state.main
const mapDispatchToProps = {
    changeView,
    changePanel,
    changeFetchedUser,
    changeAuthToken
}

export default ReactConnect(mapStateToProps, mapDispatchToProps)(App);