const ACTIONS = {
    CHANGE_VIEW: "MAIN_CHANGE_VIEW",
    CHANGE_PANEL: "MAIN_CHANGE_PANEL",
    CHANGE_FETCHED_USER: "MAIN_CHANGE_FETCHED_USER",
    CHANGE_ACTIVE_TAB: "MAIN_CHANGE_ACTIVE_TAB",
    CHANGE_USER_FRIENDS: "MAIN_CHANGE_USER_FRIENDS",
    CHANGE_AUTH_TOKEN: "MAIN_CHANGE_AUTH_TOKEN"
};

const initialState = {
    activeView: 'main',
    activePanel: 'main',
    activePanelProps: null,
    activeTab: 'groups',
    fetchedUser: null,
    authToken: null
};

const MainReducer = (state = initialState, action) => {
    const payload = action.payload;
    switch (action.type) {
        case ACTIONS.CHANGE_VIEW:
            return {
                ...state,
                activeView: payload.view,
                activePanel: payload.panel
            };
        case ACTIONS.CHANGE_PANEL:
            return {
                ...state,
                activePanel: payload.panel,
                activePanelProps: payload.data
            };
        case ACTIONS.CHANGE_ACTIVE_TAB:
            return {
                ...state,
                activeTab: payload
            };
        case ACTIONS.CHANGE_FETCHED_USER:
            return {
                ...state,
                fetchedUser: payload
            };
        case ACTIONS.CHANGE_AUTH_TOKEN:
            return {
                ...state,
                authToken: payload
            };
        default:
            return state
    }
};

export const changeView = (view, panel) => dispatch => {
    dispatch({
        type: ACTIONS.CHANGE_VIEW,
        payload: {
            view: view,
            panel: panel
        }
    });
};

export const changePanel = (panel, data) => dispatch => {
    dispatch({
        type: ACTIONS.CHANGE_PANEL,
        payload: {
            panel,
            data
        }
    });
};

export const changeAuthToken = token => dispatch => {
    dispatch({
        type: ACTIONS.CHANGE_AUTH_TOKEN,
        payload: token
    });
};

export const changeActiveTab = tab => async dispatch => {
    dispatch({
        type: ACTIONS.CHANGE_ACTIVE_TAB,
        payload: tab
    });
};

export const changeFetchedUser = fetchedUser => dispatch => {
    dispatch({
        type: ACTIONS.CHANGE_FETCHED_USER,
        payload: fetchedUser
    })
};

export default MainReducer;