import React, {Fragment, useState} from 'react';
import {connect as ReduxConnect} from 'react-redux';
import {PanelHeader, HeaderButton, platform, IOS} from '@vkontakte/vkui';
import {changePanel} from '../../Redux/mainReducer'
import {getUserGroups} from '../../vk-api';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import UserGroupsList from '../../Containers/UserGroupsList';

const osname = platform();

const FriendGroupsPanel = props => {

    const {id, fullname} = props.activePanelProps;
    const {authToken, changePanel} = props;
    const [friendGroups, setFriendGroups] = useState([]);

    const getFriendGroups = id => {
        if (friendGroups.length === 0) {
            getUserGroups(authToken, id).then(res=>{
                setFriendGroups( res.data.response.items );
            });
            return (null);
        } else {
            return (
                <UserGroupsList userGroups={friendGroups}/>
                );
        }
    };

    [1,2,3].join();

    return (
        <Fragment>
            <PanelHeader
            addon={<HeaderButton onClick={()=>{changePanel('main', {})}}>Назад</HeaderButton>}
            left={<HeaderButton onClick={()=>{changePanel('main', {})}}>{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}
            >{fullname}
            </PanelHeader>
            <div>{getFriendGroups(id)}</div>
        </Fragment>
    )
};

const mapStateToProps = state => state.main;
const mapDispatchToProps = {
    changePanel
};

export default ReduxConnect(mapStateToProps, mapDispatchToProps)(FriendGroupsPanel);