import connect from '@vkontakte/vkui-connect-promise';

export const getUserGroups = async (authToken, userId) => {
    return await connect.send("VKWebAppCallAPIMethod", {
        "method": "groups.get",
        "params": {
            "user_id": userId,
            "fields": "members_count",
            "extended": true,
            "v": "5.95",
            "access_token": authToken
        }
    });
}

export const getGroupsById = async (authToken, groupsIds) => {
    return await connect.send("VKWebAppCallAPIMethod", {
        "method": "groups.getById",
        "params": {
            "group_ids": String(groupsIds),
            "fields": "members_count",
            "v": "5.95",
            "access_token": authToken
        }
    });
}

export const getUserFriendsGroups = async (authToken, friends) => {
    return await connect.send("VKWebAppCallAPIMethod", {
        "method": "execute.getUserFriendGroups",
        "params": {
            "friends": String(friends),
            "v": "5.95",
            "access_token": authToken
        }
    });
}

export const getUserFriends = async (authToken, userId) => {
    return await connect.send("VKWebAppCallAPIMethod", {
        "method": "friends.get",
        "params": {
            "user_id": userId,
            "order": "name",
            "fields": "photo_100",
            "name_case": "nom",
            "v": "5.95",
            "access_token": authToken
        }
    });
}