import React from 'react';
import {Avatar, List, Cell} from '@vkontakte/vkui'

const UserGroupsList = (props) => {

    const {userGroups} = props;

    return (
        <List>
            {userGroups.map(group => {
                return (
                        <a style={{textDecoration: 'inherit', color:'inherit'}} href={`https://vk.com/club${group.id}`}>
                    <Cell
                        key={group.id}
                        before={< Avatar src = {
                        group.photo_200
                    } />}
                        description={`${group.members_count} Подписчиков`}>{group.name}</Cell>
                        <hr style={{display:'none'}}/>
                        </a>
                )
            })
}
        </List>
        
    )
}

export default UserGroupsList;