import React, {Fragment} from 'react';
import {Tabs, TabsItem, HorizontalScroll, PanelHeader, FixedLayout, Group, Div, InfoRow, Progress } from '@vkontakte/vkui';
import {connect as ReactConnect} from 'react-redux';

import {changeActiveTab} from '../../Redux/mainReducer';
import {fetchUserGroups, fetchUserFriends, fetchUserFriendsGroups} from '../../Redux/userReducer'
import UserGroupsList from '../../Containers/UserGroupsList';
import UserFriendsList from '../../Containers/UserFriendsList';

const MainPanel = props => {

    const {authToken, fetchedUser, activeTab, 
        changeActiveTab, fetchUserGroups, userGroups,
         userGroupsIsLoading, fetchUserFriends, userFriends,
          userFriendsIsLoading, userFriendsGroups, userFriendsGroupsIsLoading,
          fetchUserFriendsGroups, userFriendsGroupsAnalyzeProgress} = props;

    if (authToken && fetchedUser){
        if (!userGroups && !userGroupsIsLoading){
            fetchUserGroups(authToken, fetchedUser.id);
        }

        if (!userFriends && !userFriendsIsLoading){
            fetchUserFriends(authToken, fetchedUser.id);
        }

        if (userGroups && userFriends && !userFriendsGroups && !userFriendsGroupsIsLoading){
            fetchUserFriendsGroups(authToken, userGroups, userFriends);
        }
    }

    const renderTab = () => {
        switch (activeTab) {
            case "groups":
                return (<UserGroupsList userGroups={userGroups === null ? [] : userGroups}/>)
            case "friends":
                return (<UserFriendsList userFriends={userFriends === null ? [] : userFriends}/>)
                case "friendsRecommendations":
                if (!userFriendsGroups){
                    return(
                        <Group>
                        <Div>
                          <InfoRow title="Анализирую твоих друзей...">
                            <Progress value={userFriendsGroupsAnalyzeProgress} />
                          </InfoRow>
                        </Div>  
                      </Group>
                      )
                }
                return <UserGroupsList userGroups={userFriendsGroups}/>
            default:
                return (null)
        }
    }

    return (
        <Fragment>
            <PanelHeader noShadow={true}>Мой паблик</PanelHeader>
            <FixedLayout vertical="top">
                <Tabs type="buttons" theme="header">
                    <HorizontalScroll>
                        <TabsItem
                            onClick={() => {
                            changeActiveTab('groups')
                        }}
                            selected={activeTab === 'groups'}>
                            Мои группы
                        </TabsItem>
                        <TabsItem
                            onClick={() => {
                            changeActiveTab('friends')
                        }}
                            selected={activeTab === 'friends'}>
                            Мои друзья
                        </TabsItem>
                        <TabsItem
                            onClick={() => {
                            changeActiveTab('friendsRecommendations')
                        }}
                            selected={activeTab === 'friendsRecommendations'}>
                            Рекомендации
                        </TabsItem>
                    </HorizontalScroll>
                </Tabs>
            </FixedLayout>
            <div id="content" style={{
                marginTop: '48px'
            }}>{renderTab()}
            </div>
        </Fragment>
    )
}

const mapStateToProps = state => {
    return {
        ...state.main,
        userGroups: state.user.userGroups,
        userGroupsIsLoading: state.user.userGroupsIsLoading,
        userFriends: state.user.userFriends,
        userFriendsIsLoading: state.user.userFriendsIsLoading,
        userFriendsGroups: state.user.userFriendsGroups,
        userFriendsGroupsIsLoading: state.user.userFriendsGroupsIsLoading,
        userFriendsGroupsAnalyzeProgress: state.user.userFriendsGroupsAnalyzeProgress,
    }
}




const mapDispatchToProps = {
    changeActiveTab,
    fetchUserGroups,
    fetchUserFriends,
    fetchUserFriendsGroups
}
export default ReactConnect(mapStateToProps, mapDispatchToProps)(MainPanel);