import React from 'react';
import {connect as ReactConnect} from 'react-redux';
import {changePanel} from '../Redux/mainReducer';
import {Avatar, List, Cell} from '@vkontakte/vkui';

const UserFriendsList = (props) => {

    const {userFriends, changePanel} = props;

    return (
        <List>
            {userFriends.map(friend => {
                return (
                        <Cell
                        onClick={() => {
                        changePanel('friendGroups',{id: friend.id, fullname: `${friend.first_name} ${friend.last_name}`});
                    }}
                        key={friend.id}
                        before={< Avatar size = {
                        48
                    }
                    src = {
                        friend.photo_100
                    } />}>
                    {friend.first_name} {friend.last_name}
                    </Cell>
                )
            })
}
        </List>
    )
}
const mapDispatchToProps = {
    changePanel
}
export default ReactConnect(null, mapDispatchToProps)(UserFriendsList);